@use "@db-ui/base/build/scss/variables" as *;
@use "@db-ui/base/build/scss/helpers/functions" as *;
@use "@db-ui/base/build/scss/font-faces" as *;
@import "db-ui-core.variables";
$fonts-path: "../fonts/";

/* project specific declarations */
body,
[class|="pl"] {
	font-family: $db-font-family-sans;
}

.db-bg-success-light-ia {
	@extend %db-bg-success-light;
}

// pl-header {
// 	font-size: to-rem($pxValue: 15.49191);
// }

pl-logo {
	align-self: flex-start !important; // shame.css
}

.pl-c-viewport-modal-wrapper,
pl-iframe {
	background-color: $db-colors-secondary-enabled !important; // shame.css
}

// Intro page
.tpl-intro {
	main section {
		padding: 1rem 3rem 0;
		@media screen and (min-width: 1156px) {
			padding: 1rem calc(50vw - 530px) 0;
		}

		// Splitted Intro Page
		&:nth-of-type(2) {
			background-color: $db-colors-neutral-bg-2-enabled;
			padding-bottom: 2.5rem;
		}
	}

	// Citation
	.intro-quotation {
		text-align: right;

		&,
		& blockquote {
			margin: 0;
		}

		blockquote {
			font-size: to-rem($pxValue: 24);
			font-weight: 700;

			&::before {
				content: "\201E";
			}

			&::after {
				content: "\201D";
			}
		}

		figcaption {
			cite {
				font-style: normal;
			}
		}
	}
}

.pl-c-category__description {
	max-width: 55em !important;
}

.tpl-intro [src^="../../images/db-ui-"] {
	float: left;
	margin-right: to-rem($pxValue: 4);
	margin-top: to-rem($pxValue: 28);
}

.pl-c-typeahead__input {
	border-left: 0;
	border-right: 0;
	border-top: 0;
}

// Related to https://github.com/db-ui/base/pull/6
.sg-swatch {
	border: #000 dashed 1px;
	display: flex;
	height: initial;
	min-height: 6em;
	padding: 4px;

	.sg-label {
		font-size: 80%;
		margin: auto;
	}
}
